function Popup({ country }) {
    return (
        <div className='popUp'>
            {console.log(country)}
            <h3><span>{country.name}</span>-<span>{country.nativeName}</span></h3>
            <div className='flag' id='big' style={{ backgroundImage: `url(${country.flag})` }}></div>
            <div className='flex capitalAndRegion'>
                <div className='info region'><h4>Region: </h4><span>{country.region}</span>  </div>
                <div className='info capital'><h4>Capital: </h4><span>{country.capital}</span>  </div>
            </div>
            <div className="flex currencyAndPopulation">
                <div className="info currency"><h4>Currency: </h4><span>{country.currencies[0].code}</span> </div>
                <div className="info "><h4>Population: </h4><span>{country.population}</span> </div>
            </div>
            <div className="info flex-center"><h4>Languages:</h4> <ul>{country.languages.map((language) => <li><span>{language.name}</span> </li>)}</ul></div>


        </div>
    )
}

export default Popup